import React from 'react'
import PropTypes from 'prop-types'
import './summary.css'
import Star from '../../../../components/Star/Star'

const summary = ({ data }) => {
  return (
    <div className="summary">
      <div className="summary-top">
        <div className="summary-item">
          <div className="summary-score">{data.remark} <Star value={Math.round(data.remark)} /></div>
          <div className="summary-title">{data.remarkCount}</div>
        </div>
        <div className="summary-item">
          <div className="summary-score">{data.recommendStr}</div>
          <div className="summary-title">V</div>
        </div>
        <div className="summary-item">
          <div className="summary-score">{data.wantCount}</div>
          <div className="summary-title title-right"></div>
        </div>
      </div>
      <div className="summaryBtn">
        <button className="summaryBtn-item btn-want"><i className="icon icon-heart"></i></button>
        <button className="summaryBtn-item btn-see"><i className="icon icon-star"></i></button>
      </div>
    </div>
  )
}

summary.propTypes = {
  data: PropTypes.object.isRequired
}

export default summary



